<template>
    <div>
    	<br>
    	<el-form :label-position="this.$store.state.panelLayout" ref="ruleForm" label-width="100px">
    		<el-row :gutter="24">
    			 <!--  -->
	            <el-col :lg="3">
	            		<el-button type="primary" @click="openCreatedTheme">主题创建</el-button>
	             	
	            </el-col>

	            <el-col :lg="6" v-if="admin">
	             	<el-form-item label="选择用户：">
		              	<get-user-data :getUserIdFn="getSetUser"></get-user-data> 
		              </el-form-item>
	            </el-col>
	            
	          </el-row>

    	</el-form>
    	<el-table
	      :data="themeData"
	      style="width: 100%" stripe border>


	      <el-table-column
	        prop="id"
	        label="ID"
	         width="80">
	      </el-table-column>


	      <el-table-column
	        prop="theme_name"
	        label="主题名字"
	         width="80"
	        >
	      </el-table-column>






	      <el-table-column
	        prop="theme_min_img"
	        label="缩略图"
	         width="80"
	        >
	        <template slot-scope="scope">
	        	<img style="width:30px;height:30px;border-radius:15px;" :src="imgpath + '/' + scope.row.theme_min_img">
	        </template>
	      </el-table-column>
	      <el-table-column
	        prop="isDelete"
	         width="180"
	        label="主题状态">

	        <template slot-scope="scope">
				<el-tag type="success" size="mini" v-if="scope.row.isDelete == 1">已发布</el-tag>
				<el-tag type="danger" size="mini" v-else>主题未发布</el-tag>
			</template>


	      </el-table-column>
	        <el-table-column
	          prop="create_time"
	           width="160"
	          label="主题创建时间">
	        </el-table-column>
	          <el-table-column
	          prop="theme_info"
	          label="主题描述">
	         </el-table-column>


	         <el-table-column
	          prop=""
	           width="280"
	          label="操作">


	          <template slot-scope="scope">
		          	<el-button-group>
						<el-button type="success" icon="el-icon-printer" size="mini" v-if="scope.row.isDelete == 0" @click="themeRelease(scope.row)">发布</el-button>
						<el-button type="danger" size="mini" icon="el-icon-delete"  v-else-if="scope.row.isDelete == 1" @click="themeClose(scope.row)">禁用</el-button>
						<el-button type="primary" icon="el-icon-edit" size="mini"  @click="theme_edit(scope.row)">编辑</el-button>
					</el-button-group>
				</template>


	         </el-table-column>



	    </el-table>
	    <!-- <get-user-data v-if="selectUserShow" :closeGetUserDataModalFn="closeGetUserDataModal" :getUserIdFn="getSetUser"></get-user-data>   -->
    </div>
</template>

<script>
  import GetUserData from '../getUserData'
  export default {
  	components: {
    	GetUserData
  	},
    data () {
    	return {
    		themeData: [],
    		selectUserShow: false,
    		imgpath: this.$store.state.staticPath,
    		admin: false,
    		userid: 0,
    		adminlevel: 0,
    		userInfo: null,
    		user: null
    	}
    },
    created () {
    	var _this = this
    	this.userInfo = this.$store.state.userInfo
    	this.adminlevel =  this.userInfo ? this.userInfo.level : 0
		if (this.userInfo) {
			if (this.userInfo.level === 1) {
				this.admin = true
			}
		}
    	_this.gettheme()
    },
    methods: {
    	getSetUser (user) {
	      this.user = [user]
	      this.userid = user.id
	      this.gettheme()
	    },
    	gettheme () {
    		var _this = this
    		_this.$get({
				type: '$get',
				obj: {
				  url: _this.api.gettheme,
				  success: function (res) {
				    if (res.data.code == 200) {
				      _this.themeData = res.data.data
				    }
				    else {
				      _this.alerter('获取菜单数据失败！','获取菜单数据',2000)
				    }

				  },
				  error: function () {
				    _this.alerter('获取菜单数据失败！','获取菜单数据',2000)
				  }
				},
				param: {
					userid: this.userid,
					adminlevel: this.adminlevel
				}
			})
    	},
    	themeRelease (item) {
    		var _this = this
    		_this.$get({
				type: '$get',
				obj: {
				  url: _this.api.closetheme,
				  success: function (res) {
				    	console.log(res)
				    if (res.data.code == 200) {
				      _this.alertsu('发布成功！')
				      _this.gettheme()
				    }
				    else {
				      _this.alerter('发布失败！')
				    }

				  },
				  error: function () {
				    _this.alerter('发布失败！')
				  }
				},
				param: {
					id: item.id,
					user_id: item.user_id,
					type: 'release'
				}
			})
    	},
    	themeClose (item) {
    		var _this = this
    		_this.$get({
				type: '$get',
				obj: {
				  url: _this.api.closetheme,
				  success: function (res) {
				    if (res.data.code == 200) {
				    	_this.alertsu('关闭成功！')
				      _this.gettheme()
				    }
				    else {
				      _this.alerter('关闭失败！')
				    }

				  },
				  error: function () {
				    _this.alerter('关闭失败！')
				  }
				},
				param: {
					id: item.id,
					user_id: item.user_id,
					type: 'delete'
				}
			})
    	},
    	openCreatedTheme () {
    		this.$router.push({
    			name: 'themeIndex'
    		})
    	},
    	theme_edit (item) {
    		this.$store.state.themeEditDataCache = item
    		this.$router.push({
    			name: 'themeIndex',
    			query: {
    				theme_id: item.id,
    				userid: item.user_id
    			}
    		})
    	}
    }
  }
</script>


